@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,700&display=swap);
html,
body {
  width: 100%;
  height: 100%;
  font-size: 16px;
  line-height: 1.24;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFCEF;
}

main {
  height: 100%;
  margin: auto;
}

#root {
  height: 100%;
}

#main {
  height: 100%;
  padding: 1em 1em 2em; }

section {
  border-bottom: 1px solid darkolivegreen; }

section + section {
  margin-top: 2em; }

.title {
  margin-top: 0;
  font-size: 1.5em;
  line-height: 1.7em; }

.label {
  display: flex;
  align-items: center;
  padding: 0.5em;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen; }
  .label.subtotal {
    background-color: #efece1; }
  .label.total {
    background-color: #d6d3c7; }

.input {
  display: flex;
  align-items: center;
  padding: 0.5em;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen;
  border-right: 1px solid darkolivegreen; }

.input-field {
  position: relative;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background-color: inherit;
  font: inherit;
  resize: none; }
  .input-field[type="number"] {
    text-align: right; }

#earnings,
#deductions,
#additions,
#final {
  position: relative;
  grid-template-columns: 50% 50%;
  display: grid;
  border-bottom: 1px solid darkolivegreen;
  background-color: #FFFCEF; }
  #earnings .label,
  #deductions .label,
  #additions .label,
  #final .label {
    grid-column: 1 / 2; }
  #earnings .input,
  #deductions .input,
  #additions .input,
  #final .input {
    grid-column: 2 / 3; }

#earnings {
  grid-template-rows: repeat(5, 1fr); }

#deductions {
  grid-template-rows: repeat(4, 1fr); }

#additions {
  grid-template-rows: repeat(2, 1fr); }

#final {
  margin-bottom: 2em;
  border-bottom: 1px solid darkolivegreen; }

.header {
  grid-column: 1 / 3;
  grid-row: 1;
  display: flex;
  justify-content: center;
  padding: 0.5em 1em;
  align-items: center;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen;
  border-right: 1px solid darkolivegreen;
  text-transform: uppercase; }

.output {
  display: flex;
  align-items: center;
  grid-column: 2 / 3;
  padding: 0.5em;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen;
  border-right: 1px solid darkolivegreen; }
  .output span {
    text-align: right;
    width: 100%; }
  .output.subtotal {
    background-color: #efece1; }
  .output.total {
    background-color: #d6d3c7; }

.radio-label {
  grid-column: 1 / 2;
  display: flex;
  align-items: flex-start;
  padding: 0.5em;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen; }

.radio-choices {
  grid-column: 2 / 3;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen;
  border-right: 1px solid darkolivegreen; }

.radio-choice {
  padding: 0 0.5em; }

.radio-choice + .radio-choice {
  border-top: 1px solid darkolivegreen; }

.radio-choice-field {
  margin: 0;
  margin-top: 1em;
  vertical-align: top; }

.radio-choice-label {
  display: inline-block;
  padding: 0.5em;
  padding-right: 0; }

.infoTable {
  position: relative;
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #FFFCEF; }
  .infoTable .label {
    grid-column: 1 / 2; }
  .infoTable .input {
    grid-column: 2 / 3; }

#rightMiddle .input-field {
  text-align: right; }

section.details > div:nth-last-child(n + 1) {
  border-bottom: 1px solid darkolivegreen; }

.signature {
  grid-column: 1 / span 2;
  padding: 0.5em 1em 6em;
  border-top: 1px solid darkolivegreen;
  border-left: 1px solid darkolivegreen;
  border-right: 1px solid darkolivegreen; }

#nextSteps {
  align-items: flex-start;
  border: 0;
  cursor: pointer;
  display: flex; }
  #nextSteps a {
    color: inherit;
    text-decoration: none; }

.formButton {
  border-right: 1px solid darkolivegreen;
  color: #FFFCEF;
  background-color: darkolivegreen;
  cursor: pointer;
  width: 100%; }
  .formButton a,
  .formButton button {
    display: block;
    padding: 1em;
    text-align: inherit;
    width: 100%; }
  .formButton + .formButton {
    margin-left: 1em; }
  .formButton:active {
    background-color: black; }
  .formButton:nth-child(2n) {
    text-align: right; }

.exportButton {
  padding: 0;
  border: 0;
  background-color: inherit;
  color: inherit;
  text-decoration: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

.exportText {
  display: inline-block;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

.homepageContainer {
  display: block;
  width: 100%;
  height: 100%;
  padding: 16px; }

.button {
  display: inline-block;
  padding: 16px;
  margin: auto;
  border: 0;
  text-align: center;
  text-decoration: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: #FFFCEF;
  background-color: darkolivegreen; }
  .button + .button {
    margin-top: 16px; }
  .button:active {
    background-color: black; }

